@import url(https://fonts.googleapis.com/css?family=Hammersmith+One);
@import url(https://fonts.googleapis.com/css?family=Monda|Hammersmith+One);
@import url(https://fonts.googleapis.com/css?family=Monda|Sintony);
@import url(https://fonts.googleapis.com/css?family=Monda|Sintony|Raleway);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h3 {
  margin: 0px;
}

.page-title {
  text-align: center;
  font-family: Hammersmith One;
  font-size: 6vw;
  color: #494e6b;
  padding: 10px;
  background-image: url(/static/media/header.7a03f8fd.png);
  background-position: 60% 110%;
  background-repeat: no-repeat;
  background-size: 70%;
  height: 12vw;
}

.content {
  margin: 0 10% 0 10%;
  background-color: #FFF;
}

.header {
	width: 100vw;
	height: 100vh;
	top: 0;
	left: 0;
  display:flex;
  align-items: center;
  justify-content: center;
  background: #363a4e;
}

.title {
  position: relative;
  display: flex;
  justify-content: flex-end;
  color: #fff;
  font-size: 4vw;
}
/* Word + SVG styles */
.word {
	position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    margin: 0 0 0.25em 0;
    cursor: default;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    grid-area: title;
    font-size: 11vw;
    font-weight: 400;
    color: #fff;
    font-family: 'Hammersmith One', sans-serif;
}

.word .char2,
.word .char9 {
	color: #f7882f;
}

.title {
  color: #fff;
  font-family: 'Monda', sans-serif;
}

.title span, .word span {
	display: block;
	position: relative;
	flex: none;
	white-space: pre;
}



.shapes {
	position: absolute;
	top: 0;
	left: 0;
  width: 100vw;
  height: 100vh;
}

.wrapper {
  margin-left: 10%;
  margin-right: 10%;
  height: 100%;
}

.project {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.preview {
  justify-content: left;
}
.thumbnail {
  flex: 1 1 100%;
}

.right {
  flex: 1 1 100%;
}

.desc {
  margin-bottom: 10px;
  font-family: Sintony;
  color: #888888;
  font-size: 80%
}

.project-title {
  margin: 0px;
  font-family: Monda;
  color: #494949;
  font-size: 150%
}

.technologies {
  font-family: Sintony;
  font-size: 80%;
  color: #ce893d;
  border-top: 1px solid #cccccc;
  border-bottom: 1px solid #cccccc;
  padding: 3px 0px;
  margin-bottom: 6px;
}

.project > * {
  flex: 1 1 100%;
}

.contracted .preview div.desc {
  max-height: 100px;
  overflow-y: hidden;
}

.project-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
}

.readMoreLess {
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid #dfdfdf;
  font-family: Sintony;
  color: #ce893d;
  font-size: 80%;
  margin-bottom: 15px;
}

.readMoreLess:hover {
  background-color: #3d4158;
  cursor: pointer;
}

.fas {
  padding-right: 10px;
}

@media screen and (min-width: 400px) {
  .preview {
    justify-content: center;
  }

  .thumbnail {
     flex: 1 1 27%;  /*grow | shrink | basis */
     padding-right: 20px;
     max-width: 180px;
  }

  .thumbnail img {
      display: block;
      width: 100%;
      max-width: 350px; /*actual image width*/
      height: auto; /* maintain aspect ratio*/
      margin: auto; /*optional centering of image*/
  }

.right {
  flex: 1 1 73%;
}

}

* {
  box-sizing: border-box;
}

.nav {
  height: auto;
  width: 100%;
  background-color: #494e6b;
  position: relative;
}

.nav > .nav-header {
  display: inline;
}

.fixed-nav {
  position: fixed;
  top: 0;
}

.brand {
  background-color: #f7882f;
  color: #fff !important;
  width: 18%;
  text-align: center;
}

a:hover.brand {
  text-decoration: none !important;
}

.nav > .nav-btn {
  display: none;
}

.nav > .nav-links {
  display: flex;
  justify-content: space-around;
}

.nav > .nav-links > a {
  font-family: Raleway;
  display: inline-block;
  padding: 13px 10px 13px 10px;
  text-decoration: none;
  color: #f7c331;
  font-size: 2vw;
}

.nav > .nav-links > a:hover {
  text-decoration: underline;
}

.nav > #nav-check {
  display: none;
}

@media (max-width:600px) {
  .brand {
    width: auto;
    text-align: left;
  }

  .nav > .nav-btn {
    display: inline-block;
  }
  .nav > .nav-btn > label {
    display: inline-block;
    width: 50px;
    height: 50px;
    padding: 13px;
  }
  .nav > .nav-btn > label:hover {
    background-color: rgba(0, 0, 0, 0.3);
  }
  .nav > .nav-btn > label > span {
    display: block;
    width: 25px;
    height: 10px;
    border-top: 2px solid #eee;
  }
  .nav > .nav-links {
    position: absolute;
    display: block;
    width: 100%;
    background-color: #333;
    height: 0px;
    transition: all 0.3s ease-in;
    overflow-y: hidden;
    top: 50px;
    left: 0px;
  }
  .nav > .nav-links > a {
    display: block;
    width: 100%;
    font-size: 4vw;
  }
  .nav > #nav-check:not(:checked) + .nav-links {
    height: 0px;
  }
  .nav > #nav-check:checked + .nav-links {
    height: auto;
    overflow-y: auto;
  }
}

